body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
  background: #780206;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #061161, #780206);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#061161), to(#780206));
  background: -webkit-linear-gradient(top, #061161, #780206);
  background: -o-linear-gradient(top, #061161, #780206);
  background: linear-gradient(to bottom, #061161, #780206); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
   
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  

}

a:link{
  color: white;
}



